export class CardsData {
    constructor(events) {
        this.events = events;
    }
    set card(cards) {
        this._card = cards;
    }
    get card() {
        return this._card;
    }
    getCardById(cardId) {
        return this._card.find((item) => item.id === cardId);
    }
    set preview(cardId) {
        if (!cardId) {
            this._preview = null;
            return;
        }
        const selectedCard = this.getCardById(cardId);
        if (selectedCard) {
            this._preview = cardId;
            this.events.emit('card:selected');
        }
    }
}
