//В классе есть следующие методы:
// - resetData():void - очистить введенные данные после успешного оформления заказа
// - choosePaymentAdress(data:TAdressPayment): void - выбрать способ оплаты и ввести адрес
// - chooseEmailPhone(data:TEmailPhone): void - вессти емейл и телефон
// - setValid(isValid: boolean): void - проверяет объект с данными пользователя на валидность (введена ли ифнормация или нет)
export class UserInfoData {
    constructor(events) {
        this.events = events;
    }
    set userInfo(info) {
        this.paymentMethod = info.paymentMethod;
        this.address = info.address;
        this.email = info.email;
        this.telephone = info.telephone;
    }
    choosePaymentAdress(data) {
        this.paymentMethod = data.paymentMethod;
        this.address = data.address;
        return;
    }
    chooseEmailPhone(data) {
        this.email = data.email;
        this.telephone = data.telephone;
        return;
    }
    setValid(isValid) {
        if (!isValid) {
            this.events.emit('order:validation');
        }
    }
}
